import React, { Component } from 'react'

export default class Projects extends Component {
  render() {
    return (
      <div>
				<section className="colorlib-work" data-section="projects">
					<div className="colorlib-narrow-content">
						<div className="row">
							<div className="col-md-4 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
								<span className="urbanist-500">What I can do...</span>
								<h2 className="urbanist-italics-500 animate-box">Recent Projects</h2>
							</div>
						</div>
						<div className="row">
							<div className="col-md-6 animate-box" data-animate-effect="fadeInLeft">
								<div className="project" style={{backgroundImage: 'url(images/pcie.jpg)', backgroundPosition: 'top'}}>
									<div className="desc">
										<div className="con">
											<h3><a href="work.html">[CURRENT] 48Gbps PCIe RTL/Driver Code/User Application Development</a></h3>
											<span>Leading a small group of researchers to build a PCIe interface for Terahertz communication research.<br /><br />  Processing 8GHz of bandwidth in real-time that are transmitted and received with 140GHz front-end antennas. Using AXI-Stream with DMA to allow for efficient PL to PS mapping. Writing driver code for both FPGA and Computer to utilize 32 PCIe lanes. 
											Once sent to the driver code on Computer-side, the end goal is to display real-time video that is sent wirelessly from one FPGA to another. </span>
										</div>
									</div>
								</div>
							</div>
							<div className="col-md-6 animate-box" data-animate-effect="fadeInRight">
								<div className="project" style={{backgroundImage: 'url(images/proj1.PNG)', backgroundPosition: 'top'}}>
									<div className="desc">
										<div className="con">
											<h3><a href="work.html">Therahertz High-Speed Networking Tool</a></h3>
											<span>A Windows-based C++ application made in conjunction with QT to handle big data packets and efficiently graph them. Processes data coming in from DMA and AXI-Stream modules in the physical layer. Designed to save down-time for internal THz platform testing as well as to show wireless results in demo reels. <br /><br />
											Features a custom-designed GUI, multi-channel I and Q stream support, and multi-carrier constellation support. </span>
											<p className="icon">
												<span><a href="https://raw.githubusercontent.com/SergeyNEU/SergeyNEU.github.io/master/images/proj1.PNG" target="_blank" rel="noopener noreferrer"><i className="icon-link2" /> Project Image</a></span>	
											</p>
										</div>
									</div>
								</div>
							</div>
							<div className="col-md-6 animate-box" data-animate-effect="fadeInLeft">
								<div className="project" style={{backgroundImage: 'url(images/rotaryLib.jpg)', backgroundPosition: 'top'}}>
									<div className="desc">
										<div className="con">
											<h3><a href="work.html">Rotary Library</a></h3>
											<span>A USB-RS232 C++ Rotary Function library actively used in Terahertz communications research (such as channel sounding applications). Made to specifically control StepNet amplifiers to rotate front-ends on two axes (altitude/azimuth). <br /><br />
											Features a well-documented command library, real-time adjustable axis limitations, and fail-safe command sequencing to prevent users from damaging expensive equipment.
											</span>
										</div>
									</div>
								</div>
							</div>
							<div className="col-md-6 animate-box" data-animate-effect="fadeInRight">
								<div className="project" style={{backgroundImage: 'url(images/protractor.jpg)', backgroundPosition: 'top'}}>
									<div className="desc">
										<div className="con">
											<h3><a href="work.html">Digital Protractor</a></h3>
											<span>The angle of a DE1SoC board is shown via a 7-segment display and displayed in the system terminal. Utilized C+ to obtain position measurements and Quartus Prime to create a hardware circuit that converted measurements to show the inclination angle of the board.</span>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</section>
      </div>
    )
  }
}
