import React, { Component } from 'react'

export default class Work extends Component {
  render() {
    return (
      
      <div>
        <section className="colorlib-experience" data-section="work">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="urbanist-500">Doing what I love most.</span>
                <h2 className="urbanist-italics-500 animate-box">Work experience</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon work-1">
                        <i className="icon-code" />
                      </div>
                      <div className="timeline-label urbanist-500">
                        <h2>Kythera Space Solutions | Software Engineer<span> July 2021 - December 2021</span></h2>
                        <p> Software engineer in charge of supporting capacity planning optimization (CPO) software as well as developing the Kythera Operating System (KOS).<br />
                            - Executed factory acceptance tests, led daily standup meetings, introduced Python script automation, and re-structured the internal wiki to streamline future onboarding.<br />
                            - Using QT/C++, maintained code and added enhancement features in to be used in the CPO planner.<br />
                            - Partook in the front-end development of KOS: written in React with CSS-in-JS, developed initial UI, and implemented data-layer logic with React-Redux.  
                        </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon work-2">
                        <i className="icon-signal" />
                      </div>
                      <div className="timeline-label urbanist-500">
                        <h2>Ultrabroadband Nanonetworking Lab | Research Assistant <span> February 2021 - Present</span></h2>
                        <p>Used state-of-the-art RFSoC board to process 8GHz of bandwidth in real-time; transmitted and received with 140Ghz front-end antennas.<br />
                          - <b>Created RotaryLib</b>, a C++ library used to control StepNet motors. Ported library to Matlab to increase compatibility with existing testing standards. Used in two ongoing projects - Channel Sounding and Video over Terahertz.<br />
                          - <b>Developed an RFSoC Dashboard</b>, a custom high-performance dashboard used to diagnose and show I/Q streams; built using QT/C++ for framework and CSS for styling. Includes custom packet processing, interactive graphs, and direct driver support (using Dynamic Memory Allocation/Axi-Stream). Planned features include packet detection and demo-oriented charts.<br />
                          - <b>Implementing a PCIE interface to enable ultra high-bandwidth data transfer </b>(48Gbps+). Includes RTL/Verilog (with AXI-STREAM/DMA/PCIE modules) setup, driver setup (in C), as well as a user-space application (C++/CSS) to fully integrate the Video over Terahertz project.
                        </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon work-3">
                        <i className="icon-device-desktop" />
                      </div>
                      <div className="timeline-label urbanist-500">
                        <h2>Quality Software Systems | IT Support Technician <span> June 2016 - November 2015</span></h2>
                        <p>Worked with the CEO and manager to <b>build and maintain a fleet of servers and workstations for 30 programmers.</b><br />
                          - According to financial constraints; selected components, built computers, and installed any necessary software for each programmer.<br />
                          - <b>Reduced costs by 40%</b> associated with both workstation and server installation/upkeep. 
                          </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon work-4">
                        <i className="icon-book2" />
                      </div>
                      <div className="timeline-label urbanist-500">
                        <h2>Fort Lee Public Library | Class Coordinator and Library Assistant <span> June 2018 - December 2019</span></h2>
                        <p> During the summer, I started volunteering t o help elderly citizens with the goal of helping them understand how to use technology.<br />
                          - Coordinated with librarians to <b>start a Technology 101 class</b> to expand the technology assistance program and allow more elderly the opportunity to learn.<br />
                          - Following the success of the Technology 101 class, I also began <b>teaching a Networking 101 class</b> with the aim to educate people on Internet usage and safety.
                        </p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
